ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

li {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f4f4ff;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  padding: 1rem;
  transition: transform 0.2s ease, background-color 0.3s ease;
}

li:hover {
  background-color: #e8e8ff;
  transform: scale(1.02);
}

.card-info {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.card-info span {
  font-size: 0.9rem;
  color: #333;
}

button {
  padding: 0.5rem 1rem;
  font-size: 0.9rem;
  background-color: #6375ff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #4758a3;
}

button.btn-delete {
  background: none;
  color: #ff4d4d;
  font-size: 1.2rem;
  font-weight: bold;
}
